import classNames from 'classnames';
import React, { useContext, useEffect, useReducer } from 'react';
import { useSwipeable } from 'react-swipeable';
import { Cancel, Download, ExitFullscreen, Fullscreen } from '../assets/svgIcons';
import SocialLinks from '../components/SocialLinks';
import Spinner from '../components/Spinner/Spinner';
import { ModalContext, SiteDataContext } from '../utils/context';
import { useFullscreenStatus } from '../utils/utils';

const reducer = (state, action) => {
  switch (action.type) {
    case 'initialize':
      return { ...state, initialized: true };
    case 'close':
      return { initialized: false, loadedState: false, showImage: false };
    case 'loadImage':
      return { initialized: true, loadedState: true, showImage: true };
    case 'changeImage':
      return { ...state, loadedState: false };
    case 'showGlobalSpinner':
      return { ...state, initialized: false, showImage: false };
    default:
      return state;
  }
};

const ImageViewer = ({ image = {}, show, onClose, data, onPrev, onNext, setBgImage }) => {
  const [{ initialized, loadedState, showImage }, dispatch] = useReducer(reducer, {
    initialized: false,
    loadedState: false,
    showImage: false,
  });

  const { modal, setModal } = useContext(ModalContext);
  const siteData = useContext(SiteDataContext);
  const { authorData } = siteData;

  let isFullscreen, setIsFullscreen;
  const imageViewerContent = React.useRef(null);
  let fullScreenError;
  try {
    [isFullscreen, setIsFullscreen] = useFullscreenStatus(imageViewerContent);
  } catch (e) {
    fullScreenError = 'Fullscreen not supported';
    isFullscreen = false;
    setIsFullscreen = undefined;
  }

  const visibleClass = show ? 'is-visible' : undefined;
  const prevDisabled = data.findIndex((e) => e.id === image.id) === 0 || !loadedState;
  const nextDisabled = data.findIndex((e) => e.id === image.id) === data.length - 1 || !loadedState;

  const modifier = !initialized ? 'global' : '';
  const fullscreenClass = isFullscreen ? 'fullscreen' : undefined;
  const blurClass = modal.show ? 'blur' : undefined;
  const loadedClass = showImage ? 'loaded' : 'hidden';

  const handleExitFullscreen = () => document.exitFullscreen();

  const hasSocials = (author) => {
    const socialData = authorData.find((item) => author === item.authorNick);
    const socials = ['flickr', 'twitter', 'instagram', 'steam', 'othersocials'];
    const socialExists = (item) => socialData[item].length;

    return socials.some(socialExists);
  };

  const showAuthorWindow = (author) => {
    const socialData = authorData.find((item) => author === item.authorNick);
    const modalComponent = (
      <div className="about-modal-content">
        <h2>
          <img src={socialData.authorsAvatarUrl} alt="avatar" /> {socialData.authorNick}
        </h2>
        <SocialLinks data={socialData} />
        <button
          className="close"
          onClick={() => setModal({ ...modal, show: false, className: '' })}
        >
          <Cancel />
        </button>
      </div>
    );
    setModal({
      show: true,
      component: modalComponent,
      className: 'author-social-content',
      withClose: false,
    });
  };

  const toDataURL = (url) =>
    fetch(url)
      .then((response) => response.blob())
      .then(
        (blob) =>
          new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onloadend = () => resolve(reader.result);
            reader.onerror = reject;
            reader.readAsDataURL(blob);
          }),
      );

  const handleImageDownload = (source) => {
    const proxySource = `https://cors-anywhere.herokuapp.com/${source}`;
    toDataURL(proxySource).then((dataUrl) => {
      const a = document.createElement('a');
      a.href = dataUrl;
      a.download = '';
      a.click();
    });
  };

  const handlePrev = React.useCallback(
    (event) => {
      event?.stopPropagation();
      dispatch({ type: 'changeImage' });
      onPrev();
    },
    [onPrev],
  );

  const handleNext = React.useCallback(
    (event) => {
      event?.stopPropagation();
      dispatch({ type: 'changeImage' });
      onNext();
    },
    [onNext],
  );

  const handleClose = React.useCallback(() => {
    if (isFullscreen) {
      document.exitFullscreen();
    } else {
      dispatch({ type: 'close' });
      onClose();
    }
  }, [isFullscreen, onClose]);

  const handleLoad = () => {
    dispatch({ type: 'loadImage' });
    setBgImage(image);
  };

  const handleKeyboard = React.useCallback(
    (event) => {
      const { key } = event;

      if (!show) {
        return false;
      }

      switch (key) {
        case 'ArrowRight':
          if (nextDisabled) {
            return false;
          }
          return handleNext(event);
        case 'ArrowLeft':
          if (prevDisabled) {
            return false;
          }
          return handlePrev(event);
        case 'Escape':
          if (isFullscreen || modal.show) {
            return false;
          }
          return handleClose();
        case 'f':
          if (fullScreenError) {
            return false;
          }
          return setIsFullscreen();
        default:
          return false;
      }
    },
    [
      modal,
      isFullscreen,
      handleClose,
      handleNext,
      handlePrev,
      nextDisabled,
      prevDisabled,
      setIsFullscreen,
      fullScreenError,
      show,
    ],
  );

  useEffect(() => {
    window.addEventListener('keyup', handleKeyboard);

    // remove listener when component unmounts
    return () => {
      window.removeEventListener('keyup', handleKeyboard);
    };
  }, [initialized, handleKeyboard]);

  const swipeConfig = {
    delta: 10,
    preventDefaultTouchmoveEvent: true,
    trackTouch: true,
    trackMouse: false,
    rotationAngle: 0,
  };

  const handlers = useSwipeable({
    onSwipedLeft: () => {
      if (nextDisabled) {
        return false;
      }
      dispatch({ type: 'showGlobalSpinner' });
      return handleNext();
    },
    onSwipedRight: () => {
      if (prevDisabled) {
        return false;
      }
      dispatch({ type: 'showGlobalSpinner' });
      return handlePrev();
    },
    onSwiping: (event) => {
      if ((event.dir === 'Right' && !prevDisabled) || (event.dir === 'Left' && !nextDisabled)) {
        imageViewerContent.current.style.left = `${event.deltaX}px`;
      } else {
        imageViewerContent.current.style.left = '0px';
      }
    },
    onSwiped: () => {
      imageViewerContent.current.style.left = '0px';
    },
    ...swipeConfig,
  });
  
  const socialsEnabled = image && image.authorName && hasSocials(image.authorName);
  const infoClass = socialsEnabled ? 'clickable' : undefined;

  return (
    <div className={classNames('image-viewer', 'framed-modal', visibleClass)} onClick={handleClose}>
      <div className="image-nav">
        <button className="image-nav-button left" disabled={prevDisabled} onClick={handlePrev}>
          [ Prev ]
        </button>
        <button className="image-nav-button right" disabled={nextDisabled} onClick={handleNext}>
          [ Next ]
        </button>
      </div>

      <div
        ref={imageViewerContent}
        className={classNames('image-viewer-content', fullscreenClass, blurClass)}
      >
        {image && (
          <>
            <img
              alt={image.gameName}
              src={image.shotUrl}
              onClick={(event) => {
                event.stopPropagation();
              }}
              onLoad={handleLoad}
              className={loadedClass}
              {...handlers}
            />
            {initialized && !isFullscreen && (
              <div
                className="shot-info"
                onClick={(event) => {
                  event.stopPropagation();
                }}
              >
                <div
                  className={classNames('info', infoClass)}
                  onClick={() => showAuthorWindow(image.authorName)}
                >
                  <div>
                      <span className="by">by</span>
                      <span className="author">{` ${image.authorName}`}</span>
                  </div>
                  <span className="title">{image.gameName}</span>
                </div>
                <div className="image-viewer-controls">
                  <button
                    className="download-button"
                    onClick={() => {
                      handleImageDownload(image.shotUrl);
                    }}
                  >
                    <Download />
                  </button>
                  {!isFullscreen && !fullScreenError && (
                    <button className="fullscreen-button" onClick={setIsFullscreen}>
                      <Fullscreen />
                    </button>
                  )}
                </div>
              </div>
            )}
            {isFullscreen ? (
              <button className="close" onClick={handleExitFullscreen}>
                <ExitFullscreen />
              </button>
            ) : (
              <button className="close" onClick={handleClose}>
                <Cancel />
              </button>
            )}
          </>
        )}
        <Spinner modifier={modifier} show={!loadedState} />
      </div>
    </div>
  );
};

export default ImageViewer;
